import React, { Component } from 'react';
import api from '../../Services/Api';
import LogoFull from "../../views/Logo/LogoFull";
import CategoryItens from "../Filter/CategoryItens/CategoryItens";
import Search from "./Search/Search";
import styles from "./Styles.module.scss";

export default class Main extends Component{

	// Armazenamento de variáveis

	state = {
		estabelecimentos: []
	}

	componentDidMount(){
		this.loadEstabelecimentos();
	}

	loadEstabelecimentos = async () => {
		const response = await api.get('/estabelecimentos.json');
        console.log(response);
		this.setState({ estabelecimentos: response.data.docs});
	};

	render (){
		const { estabelecimentos } = this.state;

		return (
            <div>
                <LogoFull />
                <div className={styles.border}>
                    <Search />
                    <CategoryItens />  
                </div>
            </div>

		);
	}
}